html,
body,
#root,
.App {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

.App {
  background-color: #111;
  display: flex;
  flex-direction: column; /* main column: dashboard + footer */
  justify-content: space-between;
  color: white;
}

.dashboard-main {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 2rem;
  color: white;
}

.dashboard-left {
  flex: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.dashboard-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 2rem;
}

.dashboard-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.dashboard-footer {
  background-color: #000;
  color: #aaa;
  text-align: center;
  padding: 0.5rem;
  font-size: 0.9rem;
  font-family: monospace;
}
